import { filter } from 'lodash';
import { Icon } from '@iconify/react';
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink } from 'react-router-dom';
// material
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Stack,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination
} from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getTransactions } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
import useAuth from '../../hooks/useAuth';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import {
  TransactionListHead,
  TransactionListToolbar,
  TransactionMoreMenu
} from '../../components/_dashboard/transaction/list';
import { fDate, fDateTime } from '../../utils/formatTime';
import { fCurrency } from '../../utils/formatNumber';
import { handlePermission } from '../../utils/handlePermission';
// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'agent', label: 'Agent', alignRight: false },
  { id: 'montant', label: 'Montant', alignRight: false },
  { id: 'balance', label: 'New Balance', alignRight: false },
  { id: 'type_transaction', label: 'Type', alignRight: false },
  { id: 'ref_code', label: 'Ref Code', alignRight: false },
  { id: 'created_on', label: 'Date', alignRight: false }
  // { id: '' }
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_transaction) => _transaction?.lotto_name.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function Transaction() {
  const { user } = useAuth();
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const dispatch = useDispatch();
  const { transaction } = useSelector((state) => state.user);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('lotto_name');
  const [filterName, setFilterName] = useState('');
  const [value, setValue] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const [rowsPerPage, setRowsPerPage] = useState(-1);
  const [loading, setLoading] = useState(false);

  const onChangeDate1 = (datavalue) => {
    setValue(datavalue);
  };
  const onChangeDate2 = (datavalue) => {
    setValue1(datavalue);
  };

  const handleGo = async () => {
    setLoading(true);
    await dispatch(getTransactions(fDate(value), fDate(value1)));
    setLoading(false);
  };

  useEffect(() => {
    handleGo();
  }, [dispatch]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = transaction?.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const handleDeleteTransaction = (transactionId) => {
    // dispatch(deleteTransaction(transactionId));
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - transaction.length) : 0;

  const filteredTransactions = applySortFilter(transaction, getComparator(order, orderBy), filterName);

  const isTransactionNotFound = filteredTransactions?.length === 0;

  return (
    <Page title="Listes des transactions: List | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Liste des transactions"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Listes des transactions', href: PATH_DASHBOARD.transaction?.root },
            { name: 'Listes' }
          ]}
          action={
            handlePermission(user?.perms, 'add_wallettransaction') && (
              <Button
                variant="contained"
                component={RouterLink}
                to={PATH_DASHBOARD.transaction?.new}
                startIcon={<Icon icon={plusFill} />}
              >
                Ajouter transaction
              </Button>
            )
          }
        />

        <Card>
          <TransactionListToolbar
            value={value}
            value1={value1}
            onChangeDate1={onChangeDate1}
            onChangeDate2={onChangeDate2}
            handleGo={handleGo}
            loading={loading}
            setLoading={setLoading}
            filterName={filterName}
            onFilterName={handleFilterByName}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <TransactionListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={[
                    ...(user?.role === 'superuser' ? [{ id: 'company', label: 'Compagnie', alignRight: false }] : []),
                    ...TABLE_HEAD
                  ]}
                  rowCount={transaction?.length}
                  numSelected={selected?.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                />
                <TableBody>
                  {(rowsPerPage > 0
                    ? filteredTransactions?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    : filteredTransactions
                  )?.map((ite) => {
                    const isItemSelected = selected.indexOf(ite?.id) !== -1;

                    return (
                      <TableRow
                        hover
                        key={ite?.id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        {user?.role === 'superuser' && (
                          <TableCell component="th" scope="row" padding="none">
                            <Stack direction="row" alignItems="center" spacing={2}>
                              <Avatar alt={ite?.companyName} src={ite?.company_photo} />
                              <Typography variant="subtitle2" noWrap>
                                {ite?.companyName}
                              </Typography>
                            </Stack>
                          </TableCell>
                        )}
                        <TableCell align="left">{ite?.name}</TableCell>
                        <TableCell align="left" sx={{ color: ite?.type_transaction === 'Recharge' ? 'green' : 'red' }}>
                          {ite?.type_transaction === 'Recharge' ? '+' : '-'} {fCurrency(ite?.montant)}{' '}
                          {ite?.currency_code}
                        </TableCell>
                        <TableCell align="left" sx={{ color: 'green' }}>
                          {fCurrency(ite?.new_balance)} {ite?.currency_code}
                        </TableCell>
                        <TableCell align="left">{ite?.type_transaction}</TableCell>
                        <TableCell align="left">{ite?.ref_code}</TableCell>
                        <TableCell align="left">{fDateTime(ite?.created_on)}</TableCell>

                        {/* <TableCell align="right">
                          <TransactionMoreMenu
                            onDelete={() => handleDeleteTransaction(ite?.id)}
                            transactionId={ite?.id}
                          />
                        </TableCell> */}
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
                {isTransactionNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <TablePagination
            labelRowsPerPage="Lignes par page"
            rowsPerPageOptions={[25, 50, 75, 100, { label: 'Tout', value: -1 }]}
            component="div"
            count={transaction?.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}
